import React from "react";
import { NavLink } from "react-router-dom";

function MainNav() {
  return (
    <nav className="main_nav">
      <NavLink to="/" exact>
        HOME
      </NavLink>
      <NavLink to="/introduce" exact>
        Introduce
      </NavLink>
      <NavLink to="/portfolio" exact>
        Portfolio
      </NavLink>
      <NavLink to="/resume" exact>
        Resume
      </NavLink>
    </nav>
  );
}
export default MainNav;
