import React from "react";
import { NavLink } from "react-router-dom";

function Portfolio() {
  return (
    <div className="p_main">
      <h1>Portfolio</h1>
      <div>
        <NavLink to="/portfolio/guestbook">
          <div>GusetBook</div>
        </NavLink>
        <NavLink to="/portfolio/meett">
          <div>Meett</div>
        </NavLink>
        <NavLink to="/portfolio/woo">
          <div>Woo</div>
        </NavLink>
      </div>
    </div>
  );
}

export default Portfolio;
